      cursor: text;
      font-size: 95%;
      opacity: 1;
      transition: all .2s;
      top: -.5em;
      left: 12px;
      z-index: 3;
      line-height: 1;
      padding: 0 1px;
      color: #EC5F57;
      font-weight: 600;
    outline: none; }
      .has-float-label label::after, .has-float-label > span::after {
        content: " ";
        display: block;
        position: absolute;
        background: white;
        height: 2px;
        top: 50%;
        left: -.2em;
        right: -.2em;
        z-index: -1; }
    .has-float-label .form-control::-webkit-input-placeholder {
      opacity: 0;
      transition: all .2s; }
    .has-float-label .form-control::-ms-input-placeholder {
      opacity: 0;
      transition: all .2s; }
    .has-float-label .form-control::placeholder {
      opacity: 0;
      transition: all .2s; }
