.contactInputBox label {
    font-size: 14px;
    opacity: .8;
    font-weight: 400;
  }

.contactInputBox input {
    font-size: 14px;
    padding: 20px 0px;
    height: 56px;
    border: none;
    border-bottom: solid 1px rgba(0, 0, 0, 0.1);
    background: #fff;
    width: 280px;
    box-sizing: border-box;
    transition: all .3s linear;
    font-weight: 400;
    -webkit-appearance: none;
    margin-right: 15px !important;
  }
  .contactInputBox input:focus {
    border-bottom: solid 1px #303D5B;
    outline: 0;
    box-shadow: 0 2px 6px -8px rgba(182, 157, 230, 0.45);
  }
  
  .contactInputBox {
    position: relative;
    margin-bottom: 10px;
    width: 70%;
  }
  .contactInputBox label {
    position: absolute;
    top: calc(50% - 7px);
    left: 0;
    opacity: 0;
    transition: all .3s ease;
    padding-left: 30px;
    font-size: 15px;
  }
  
  .contactInputBox input {
    width: calc(100% - 44px);
    margin-left: auto;
    display: -webkit-flex;
    display: flex;
  }
  
  .contactInputBox input:not(:placeholder-shown) {
    padding: 28px 0px 0px 0px;
  }
  .contactInputBox input:not(:placeholder-shown) + label {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: .7;
  }
  .contactInputBox input:valid:not(:placeholder-shown) + label {
    opacity: 1;
  }
  .contactInputBox input:valid:not(:placeholder-shown) + label {
    fill: #303D5B;
  }
  .contactInputBox input:not(:valid):not(:focus) + label {
    -webkit-animation-name: shake-shake;
            animation-name: shake-shake;
    -webkit-animation-duration: .3s;
            animation-duration: .3s;
  }
  
  @-webkit-keyframes shake-shake {
    0% {
      -webkit-transform: translateX(-3px);
              transform: translateX(-3px);
    }
    20% {
      -webkit-transform: translateX(3px);
              transform: translateX(3px);
    }
    40% {
      -webkit-transform: translateX(-3px);
              transform: translateX(-3px);
    }
    60% {
      -webkit-transform: translateX(3px);
              transform: translateX(3px);
    }
    80% {
      -webkit-transform: translateX(-3px);
              transform: translateX(-3px);
    }
    100% {
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
    }
  }
  
  @keyframes shake-shake {
    0% {
      -webkit-transform: translateX(-3px);
              transform: translateX(-3px);
    }
    20% {
      -webkit-transform: translateX(3px);
              transform: translateX(3px);
    }
    40% {
      -webkit-transform: translateX(-3px);
              transform: translateX(-3px);
    }
    60% {
      -webkit-transform: translateX(3px);
              transform: translateX(3px);
    }
    80% {
      -webkit-transform: translateX(-3px);
              transform: translateX(-3px);
    }
    100% {
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
    }
  }


  @media only screen and (max-width: 768px) {
    .contactInputBox {
        width: 100% !important;
      }
  }

